.page-balnk {
	background-color: rgb(19, 19, 19);
	color: white;

	&__container {
		display: flex;
		justify-content: center;
		align-content: center;
		flex-flow: column;
		height: 100%;
		text-transform: uppercase;
	}

	&__address {
		display: flex;
		flex-flow: column;
		justify-content: center;
		text-align: center;
	}

	&__text {
		font-weight: bold;
		margin-bottom: 24px;
	}

	&__phone, 
	&__phone:hover {
		@extend %primary-font;
		text-decoration: none;
		font-weight: bold;
		margin-top: 24px;
	}
}